* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

a {
  text-decoration: none;
}
.prime__button {
  padding: 14px 32px 14px 32px;
  background: #47cf34;

  border: 1px solid #47cf34;
  border-radius: 8px;
  font: 700 16px 'Montserrat';
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;

  transition: all 0.2s ease-in-out;
  &:hover {
    background: #30b01e;
  }
}
textarea {
  border: none;
  overflow: auto;
  outline: none;

  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;

  resize: none; /*remove the resize handle on the bottom right*/
}
