#services {
  background: white;
  overflow-x: hidden;
  .services__description {
    margin-top: 10px;
    margin-bottom: 0;
    position: relative;

    .training__bg-img {
      position: absolute;
      top: -80px;
      right: -6.66vw;
      z-index: 1;
    }

    .training__cube-img {
      position: absolute;
      top: -30px;
      left: 40px;
      z-index: 1;
    }

    .description-text {
      font: 700 44px 'Montserrat';
      line-height: 54px;
      color: #282c37;
      margin: 0 0 35px 60px;
      position: relative;
      z-index: 3;
    }

    .description-plans {
      margin: 0 0 40px 105px;

      .plans-item {
        gap: 41px;
        display: flex;
        align-items: flex-start;

        img {
          width: 23px;
          height: 25px;
        }

        p {
          font-family: 'Lato';
          font-style: normal;
          font-weight: 400;
          font-size: 17px;
          line-height: 20px;
          color: #282c37;
        }
      }
    }
  }

  .services__second-block {
    margin: 60px 0 0 62px;
    max-width: 1155px;

    .programs-text {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 700;
      font-size: 30px;
      line-height: 37px;
      color: #282c37;
      margin-bottom: 35px;
    }

    .services-descr {
      font-family: 'Lato';
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;
      color: #000000;
      margin-left: 17px;
      margin-bottom: 27px;

      span {
        color: #6ca7ef;
        cursor: pointer;

        &:hover {
          color: #0073ff;
          transition: 0.4 ease-in-out;
        }
      }
    }

    .programs-item {
      margin-bottom: 160px;
      margin-left: 35px;

      .services-ul {
        margin-left: 38px;

        .services-li {
          font-family: 'Lato';
          font-style: normal;
          font-weight: 400;
          font-size: 17px;
          line-height: 20px;
          color: #282c37;
          margin-bottom: 10px;
          max-width: 1115px;
          position: relative;
          z-index: 3;
        }

        .services-li::marker {
          font-size: 35px;
          color: #a5cbf9;
        }
      }
    }
  }
}

@media (max-width: 575.98px) {
  #services {
    padding-top: 20px;

    .breadcrumbs {
      display: none;
    }

    .services__description {
      .description-text {
        font-size: 22px !important;
        line-height: 25px !important;
      }

      .description-plans {
        margin: 0 0 40px 0 !important;
      }
    }

    .services__second-block {
      .programs-text {
        font-size: 22px !important;
        line-height: 25px !important;
        margin-bottom: 35px;
      }

      .services-descr {
        margin-left: 0 !important;
      }

      .programs-item {
        margin-bottom: 70px !important;
        margin-left: 0 !important;

        .services-ul {
          margin-left: 0 !important;
        }
      }
    }
  }
}

@media (max-width: 767.98px) {
  .description-text {
    font-size: 30px !important;
    line-height: 37px !important;
  }
}

@media (max-width: 991.98px) {
  #services {
    .services__description {
      .training__cube-img,
      .training__bg-img {
        display: none !important;
      }
      .description-text {
        font-size: 35px;
        line-height: 40px;
        margin: 0 0 20px 0;
      }

      .description-plans {
        margin: 0 0 40px 50px;

        .plans-item {
          gap: 20px;
        }
      }
    }

    .services__second-block {
      margin: 60px 0 0 0;

      .programs-text {
        margin-bottom: 35px;
      }

      .programs-item {
        margin-left: 0;
      }
    }
  }
}

@media (max-width: 1199.98px) {
  .training__bg-img {
    right: -10vw !important;
  }
}

@media (max-width: 1399.98px) {
}
